:root {
    /* Primary Brand Colors */
    --primary: 158 88% 13%;
    --primary-foreground: 30 0% 100%;
    
    /* Golden Accent */
    --accent: 35 49% 36%;
    --accent-foreground: 30 0% 100%;
    
    /* Backgrounds */
    --background: 30 0% 100%;
    --foreground: 158 88% 13%;
    
    /* Cards & Surfaces */
    --card: 30 0% 100%;
    --card-foreground: 158 88% 13%;
    
    --popover:30 0% 100%;
    --popover-foreground:158 88% 13%;
    
    /* Secondary */
    --secondary: 48 25% 93%;
    --secondary-foreground: 158 88% 13%;
    
    /* Muted */
    --muted: 30 0% 100%;
    --muted-foreground: 158 88% 13%;
    
    /* Destructive */
    --destructive: 0 84% 60%;
    --destructive-foreground:30 0% 100%;
    
    /* Borders & Inputs */
    --border: 48 20% 88%;
    --input: 48 20% 88%;
    --ring: 158 88% 13%;
    
    /* Radius */
    --radius: 0.5rem;
    
    /* Sidebar */
    --sidebar-background: 48 33% 97%;
    --sidebar-foreground:158 88% 13%;
    --sidebar-primary: 158 88% 13%;
    --sidebar-primary-foreground: 40 20% 98%;
    --sidebar-accent: 42 51% 54%;
    --sidebar-accent-foreground: 158 88% 13%;
    --sidebar-border: 48 20% 88%;
    --sidebar-ring: 158 88% 13%;
    
    /* Custom Colors */
    --cream: 48 33% 97%;
    --gold: 35 49% 36%;
    --gold-light: 35 49% 36%;
    --gold-dark: 35 49% 36%;
    --forest: 158 88% 13%;
    --forest-light: 158 88% 13%;
    --forest-dark: 158 88% 13%;
    
    /* Gradients */
    --gradient-hero: linear-gradient(135deg, hsl(155 55% 24%) 0%, hsl(155 45% 35%) 100%);
    --gradient-gold: linear-gradient(135deg, hsl(42 51% 54%) 0%, hsl(42 60% 65%) 100%);
    --gradient-cream: linear-gradient(180deg, hsl(48 33% 97%) 0%, hsl(48 25% 93%) 100%);
    
    /* Shadows */
    --shadow-sm: 0 1px 2px 0 hsl(155 30% 15% / 0.05);
    --shadow-md: 0 4px 6px -1px hsl(155 30% 15% / 0.07), 0 2px 4px -2px hsl(155 30% 15% / 0.05);
    --shadow-lg: 0 10px 15px -3px hsl(155 30% 15% / 0.08), 0 4px 6px -4px hsl(155 30% 15% / 0.05);
    --shadow-xl: 0 20px 25px -5px hsl(155 30% 15% / 0.1), 0 8px 10px -6px hsl(155 30% 15% / 0.05);
    --shadow-gold: 0 4px 14px 0 hsl(42 51% 54% / 0.25);
    
    /* Typography */
    /* --font-display: 'Poppins', serif;
    --font-body: 'Poppins', sans-serif; */
  }

*{
  border-color: hsl(var(--border));
}

html {
    scroll-behavior: smooth;
  }

body{
  background-color: hsl(var(--background));
  color: hsl(var(--foreground));
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
    font-family: var(--font-body);
}

h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-display);
  }
.container{
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 2rem;
  padding-left: 2rem;
}
@media (min-width: 1400px){

  .container{
    max-width: 1400px;
  }
}
/* Hero Button Variants */
.btn-hero-primary{
  background-color: hsl(var(--primary));
  --tw-text-opacity: 1;
  color: rgb(0 0 0 / var(--tw-text-opacity, 1));
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
  animation-duration: 300ms;
}
.btn-hero-primary:hover{
    background-color: hsl(var(--forest-light));
}
.btn-hero-primary {
    box-shadow: var(--shadow-md);
  }
.btn-hero-primary:hover {
    box-shadow: var(--shadow-lg);
    transform: translateY(-1px);
  }
.btn-hero-gold{
  font-weight: 600;
  --tw-text-opacity: 1;
  color: rgb(0 0 0 / var(--tw-text-opacity, 1));
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
  animation-duration: 300ms;
    background: var(--gradient-gold);
    box-shadow: var(--shadow-gold);
}
.btn-hero-gold:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px 0 hsl(42 51% 54% / 0.35);
  }
/* Product Card */
.product-card{
  overflow: hidden;
  border-radius: var(--radius);
  background-color: hsl(var(--card));
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
  animation-duration: 300ms;
    box-shadow: var(--shadow-sm);
}
.product-card:hover {
    box-shadow: var(--shadow-lg);
    transform: translateY(-4px);
  }
/* Glass Effect */
/* Gold Text Gradient */
/* Info Bar */
.info-bar{
  background-color: hsl(var(--primary));
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  text-align: center;
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: hsl(var(--primary-foreground));
}
/* Category Nav */
.category-nav-item{
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 500;
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 200ms;
  animation-duration: 200ms;
}
.category-nav-item:hover{
  color: hsl(var(--accent));
}
.category-nav-item.active{
  border-bottom-width: 2px;
  border-color: hsl(var(--accent));
  color: hsl(var(--accent));
}
/* Filter Sidebar */
.filter-section{
  border-bottom-width: 1px;
  border-color: hsl(var(--border));
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.filter-title{
  margin-bottom: 0.75rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.025em;
  color: hsl(var(--muted-foreground));
}
